"use client"

import {Button} from "@/components/ui/button";
import { zodResolver } from "@hookform/resolvers/zod";
import React, { useState } from 'react';

const SprintCreationForm = ({projectTitle, projectKey, projectId, sprintkey }) => {
    const [showForm, setShowForm] = useState(false);

    useForm({
        resolver: zodResolver(createSprint),
    });

    return (
        <>
            <div>
                <h1> {projectTitle} </h1>
                <Button classname="mt-2" onclick={() => setShowForm(!showForm)} variant={showForm?"destructive":"default"}> 
                    {showForm ? "Cancel" : "Create New Sprint"}
                </Button>
            </div>
            {showForm && <>form</>}
        </>);
};

export default SprintCreationForm;